import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';
import { formatPath, getApplicationKey } from '@webui-one/core/utils/path';
import {
    strip,
    truncateLongString,
} from '@webui-one/core/utils/string';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import NavigateHome from '@webui-one/styles/static/NavigateHome';
import NavigateUp from '@webui-one/styles/static/NavigateUp';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import BreadcrumbInput from './BreadcrumbInput';
import style from './BreadcrumbStyles';

const getPath = obj => (
    obj.path.indexOf(obj.parentPath) === 0
        ? obj.path.substr(obj.parentPath.length)
        : obj.path
);

class Breadcrumb extends React.Component {
    constructor(props) {
        super(props);
        this.handleNavigateBreadcrumbs = this.handleNavigateBreadcrumbs.bind(this);
        this.handleGetChildrenNames = this.handleGetChildrenNames.bind(this);
        this.handleNavigate = this.handleNavigate.bind(this);
        this.handleReset = this.handleReset.bind(this);
        this.state = { originalUrl: '', breadcrumbPath: '' };
    }

    static getDerivedStateFromProps(props, state) {
        const { url } = props;
        const { originalUrl } = state;
        return url === originalUrl ? null : { originalUrl: url, breadcrumbPath: url };
    }

    handleGetChildrenNames(matchString = '', chunkSize) {
        const { onGetChildrenNames, showActions, showOperdata } = this.props;
        const { breadcrumbPath } = this.state;
        onGetChildrenNames(breadcrumbPath, matchString, showActions, showOperdata, chunkSize);
    }

    handleNavigateBreadcrumbs(path, isBackNav) {
        const { onGetChildrenNames } = this.props;
        const { breadcrumbPath } = this.state;
        let newBreadcrumbPath;
        if (isBackNav) {
            if (breadcrumbPath === '') return;
            const pathSegments = pathToObjects(breadcrumbPath);
            newBreadcrumbPath = pathSegments[pathSegments.length - 1].parentUri;
        } else {
            newBreadcrumbPath = breadcrumbPath + path;
        }
        onGetChildrenNames(newBreadcrumbPath);
        this.setState({ breadcrumbPath: newBreadcrumbPath });
    }

    handleReset() {
        const { originalUrl } = this.state;
        this.setState({ breadcrumbPath: originalUrl });
        this.props.onReset();
    }

    buildBreadcrumbLinks(paths, isList, breadcrumbsCount) {
        const baseUrl = formatPath(getApplicationKey(window.location.pathname));
        let childIsListNode = false;
        const links = paths.reverse().reduce((acc, obj, index) => {
            if (obj.uri) {
                const path = `${baseUrl}${obj.uri}`;
                if (!(index === 0 && isList)) {
                    if (!childIsListNode) {
                        acc.push(
                            <span
                                key={`${path}/`}
                                style={
                                index === breadcrumbsCount ? style.blackLink : style.link
                                } >
                                {'/'}
                            </span>,
                        );
                    }
                }

                childIsListNode = Array.isArray(obj.keys);
                let linkText = strip(getPath(obj), '/');
                if (childIsListNode) {
                    linkText = truncateLongString(linkText);
                }

                acc.push(
                    <Link
                        style={index === breadcrumbsCount ? style.blackLink : style.link}
                        key={path}
                        to={path}
                        data-handle={`link ${path}`}
                        onClick={() => this.setState({ breadcrumbPath: obj.path })}
                        >
                        {linkText}
                    </Link>,
                );
            }
            return acc;
        }, []);
        return links.reverse();
    }

    handleNavigate(path, isBackNav) {
        if (isBackNav) this.handleBackNavigate();
        else {
            const { onNavigate, onGetChildrenNames } = this.props;
            const { breadcrumbPath } = this.state;
            if (path !== '') {
                const newBreadcrumbPath = breadcrumbPath + path;
                this.setState({ breadcrumbPath: newBreadcrumbPath });
                onNavigate(newBreadcrumbPath);
                onGetChildrenNames(newBreadcrumbPath);
            } else {
                onNavigate(breadcrumbPath);
            }
        }
    }

    handleBackNavigate() {
        const { onGetChildrenNames } = this.props;
        const { breadcrumbPath } = this.state;
        if (breadcrumbPath !== '') {
            const pathSegments = pathToObjects(breadcrumbPath);
            const newBreadcrumbPath = pathSegments[pathSegments.length - 1].parentUri;
            this.setState({ breadcrumbPath: newBreadcrumbPath });
            onGetChildrenNames(newBreadcrumbPath);
        }
    }

    render() {
        const {
            childrenNames,
            isList: tempIsList,
            rootModels,
        } = this.props;
        const {
            breadcrumbPath,
        } = this.state;
        const fullUrl = breadcrumbPath;
        const isList = fullUrl === '' ? false : tempIsList;
        const breadcrumbsPaths = strip(breadcrumbPath, '/').split(/[/{]/);
        const breadcrumbsCount = (breadcrumbsPaths.length === 1 && breadcrumbsPaths[0] === '') ?
            0 : breadcrumbsPaths.length;
        const paths = pathToObjects(`/${strip(fullUrl, '/')}`);
        const links = this.buildBreadcrumbLinks(paths, isList, breadcrumbsCount);
        const upLink = (paths && paths.length > 1) ? paths[1].uri : '/';
        const baseUrl = formatPath(getApplicationKey(window.location.pathname));
        const path = `${baseUrl}${upLink}`;
        const enabled = !!fullUrl;
        return (<div style={style.crumbs} data-handle="breadcrumb" data-breadcrumb-path={fullUrl}>
            <Link
                to={baseUrl}
                style={style.navHome}
                data-handle={`navHome${enabled ? '' : '_disabled'}`}>
                <NavigateHome color={enabled ? defaultStyles.color1 : defaultStyles.color2}/>
            </Link>
            <Link
                to={path}
                style={style.navUp}
                data-handle={`navUp${enabled ? '' : '_disabled'}`}>
                <NavigateUp color={enabled ? defaultStyles.color1 : defaultStyles.color2}/>
            </Link>
            <span style={style.blackLink}>
                {'/'}
            </span>
            { links }
            {isList && <span style={style.blackLink}>{'{'}</span>}
            <BreadcrumbInput
                onGetChildrenNames={this.handleGetChildrenNames}
                childrenNames={breadcrumbPath === '' ? rootModels : childrenNames}
                onNavigate={this.handleNavigate}
                onNavigateBreadcrumbs={this.handleNavigateBreadcrumbs}
                onReset={this.handleReset}
                path={fullUrl}
                isList={isList}
                  />
            {isList && <span style={style.blackLink}>{'}'}</span>}
        </div>);
    }
}

Breadcrumb.propTypes = {
    childrenNames: PropTypes.arrayOf(PropTypes.string),
    isList: PropTypes.bool,
    onGetChildrenNames: PropTypes.func,
    onReset: PropTypes.func,
    onNavigate: PropTypes.func,
    rootModels: PropTypes.arrayOf(PropTypes.string),
    showActions: PropTypes.bool,
    showOperdata: PropTypes.bool,
    // url: PropTypes.string, //url only used in get derived state from props
};

Breadcrumb.defaultProps = {
    url: '',
};

export default Breadcrumb;
