import HeaderContainer from 'Common/Components/Header/HeaderContainer';
import FooterContainer from 'Common/Components/Footer/FooterContainer';
import React from 'react';
import PropTypes from 'prop-types';
import BreadcrumbContainer from 'Common/Components/Breadcrumb/BreadcrumbContainer';
import { Messages, translate } from 'modules/intl';
import { CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import EditModeButtonContainer from './EditModeButtonContainer';
import FlattenButtonContainer from './FlattenButtonContainer';
import { style, optionsStyle } from './ConfigurationEditorStyles';

class ConfigurationEditor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showOptions: false,
        };
    }

    render() {
        const {
            children,
            onNavigate,
            routeParams,
            checkboxes,
            subfoldersAreDisabled,
        } = this.props;
        const { showOptions } = this.state;
        const optionsMenu =
        (<div style={optionsStyle.container} data-handle="viewOptionsMenu">
            <button
                data-handle="view-options"
                style={optionsStyle.button}
                onClick={() => this.setState({ showOptions: true })}
            >View options 
            </button>
            {showOptions && <button
                data-handle="close-view-options"
                onClick={() => this.setState({ showOptions: false })}
                onScroll={() => this.setState({ showOptions: false })}
                style={optionsStyle.overlay}
            >
                <div style={optionsStyle.scrollProvoker} />
            </button>}
            {showOptions && <div style={optionsStyle.pop}>
                <div style={optionsStyle.menuArrowShadow} />
                {checkboxes}
                <div style={optionsStyle.menuArrow} />
            </div>}
        </div>);

        const commitComponents = [
            <FlattenButtonContainer />,
            <EditModeButtonContainer />,
            optionsMenu,
        ];
        return (
            <div
                data-handle={CONFIGURATION_EDITOR_NAME}
                style={style.application}
                >
                <HeaderContainer
                    applicationName={'Configuration editor'}
                    additionalComponentsRight={commitComponents}
            >
                    <div style={style.secondHeader}>
                        <div style={style.contentWrapper}>
                            <BreadcrumbContainer
                                onNavigate={onNavigate}
                                url={routeParams.splat}
                       />
                            {subfoldersAreDisabled &&
                            <div data-handle="yangMessage" style={style.yangMessage}>
                                {translate(
                                    Messages.CONFIGURATIONEDITOR.INCLUDE_SUBFOLDERS_DISABLED,
                                    )}
                            </div>}
                        </div>
                    </div>
                    <div style={defaultStyles.shadow} />
                </HeaderContainer>
                <div style={style.backgroundContainer}>
                    <div style={style.configContainer}>
                        {children}
                    </div>
                </div>
                <FooterContainer />
            </div>
        );
    }
}

ConfigurationEditor.propTypes = {
    children: PropTypes.node,
    checkboxes: PropTypes.shape(Object).isRequired,
    onNavigate: PropTypes.func,
    routeParams: PropTypes.shape(Object).isRequired,
    subfoldersAreDisabled: PropTypes.bool,
};

export default ConfigurationEditor;
