import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router';
import Loader from '@webui-one/components/Loading/Loader';
import { formatPath } from '@webui-one/core/utils/path';
import { timeStampToISOLocale } from '@webui-one/core/utils/time';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Grid from '@webui-one/components/Grid/Grid';
import {
    RELOAD_ACTION_PATH,
} from 'modules/packages/paths';
import { CONFIGURATION_EDITOR_NAME } from 'ConfigurationEditor';
import { packagesAndModules as style } from './ConfigurationEditorStyles';

const messages = {
    'program-code-error': 'Code error',
    'java-uninitialized': 'Java uninitialized',
    'bad-ncs-version': 'Bad NSO version',
    'missing-dependency': 'Missing dependency',
    'circular-dependency': 'Circular dependency',
    'package-meta-data-error': 'Metadata error',
    'file-load-error': 'File load error',
    'erlang-error': 'Erlang error',
};

const getPackageHeader = (category, modelLinks) => {
    const showStatus = modelLinks[category].isOk ?
        !!modelLinks[category].version : !!modelLinks[category].status;
    return (
        <div key={category} data-handle="module" style={style.moduleChunk}>
            <div data-handle={category} style={style.moduleRow}>
                <div
                    style={style.modulesHeader}
                    data-handle="package-name"
          >
                    {modelLinks[category].name}
                </div>
                {showStatus && <span
                    data-handle="package-version"
                    style={modelLinks[category].isOk
                    ? style.packageVersionOk
                    : style.packageVersionError}
            >
                    {
                      modelLinks[category].isOk ?
                      `v${modelLinks[category].version}` :
                      messages[modelLinks[category].status]
                    }
                </span>}
            </div>
        </div>);
};

const renderModels = (rootModels, loadingRootModels) => (
    <div data-handle="modules" style={style.moduleLinks}>
        <div style={style.headerContainer}>
            <div style={style.header}>MODULES</div>
        </div>
        <div style={style.contentContainer}>
            <div style={style.loadingInformation}>
                {loadingRootModels && (
                    <div style={style.loader}>
                        <Loader color={defaultStyles.textColor1} />
                    </div>
                )}
            </div>
            {!loadingRootModels &&
                <Grid asGrid>
                    {rootModels.sort().map(modelName => (
                        <Link
                            to={formatPath(`${CONFIGURATION_EDITOR_NAME}/${modelName}`)}
                            key={modelName}
                            style={style.modelLink}
                        >{modelName}</Link>
                    ))}
                </Grid>
            }
        </div>
    </div>
);

const renderPackages = (
    packageLinks,
    onReload,
    reloadError,
    reloadPending,
    completedAt,
) => (
    <div data-handle="packages" style={style.moduleLinks}>
        <div style={style.headerContainer}>
            <div style={style.header}>PACKAGES</div>
            <button
                style={style.reloadButton}
                onClick={onReload}
                data-handle="reload-packages"
              >Reload</button>
        </div>
        <div style={style.contentContainer}>
            {!reloadError && (reloadPending || completedAt) &&
            <div style={style.loadingInformation}>
                {reloadPending && <div style={style.loader}>
                    <Loader color={defaultStyles.textColor1} />
                </div>}
                {completedAt && <span data-handle="completed-at" >
                  Reload completed at {timeStampToISOLocale(completedAt)}
                </span>}
            </div>}
            {reloadError && !reloadPending && completedAt &&
            <div data-handle="reload-error" style={style.reloadError}>
            An error occurred during reload. For detailed information, go to the <Link
                style={{ color: 'white' }}
                to={formatPath(RELOAD_ACTION_PATH)}>reload action
            </Link>
            </div>}
            { Object.keys(packageLinks).length > 0 ?
                <div><Grid asGrid>
                    { Object.keys(packageLinks).sort().map(
                  category => getPackageHeader(category, packageLinks),
                  ) }
                </Grid></div> : false}
        </div>
    </div>
);

const PackagesAndModules = (
    {
        packageLinks,
        onReload,
        reloadError,
        reloadPending,
        completedAt,
        rootModels,
        loadingRootModels,
    },
) => (
    <div style={style.container}>
        { renderPackages(
              packageLinks,
              onReload,
              reloadError,
              reloadPending,
              completedAt) }

        { renderModels(rootModels, loadingRootModels) }
    </div>);

PackagesAndModules.propTypes = {
    packageLinks: PropTypes.shape({}).isRequired,
    onReload: PropTypes.func.isRequired,
    reloadError: PropTypes.bool,
    reloadPending: PropTypes.bool,
    completedAt: PropTypes.number,
    rootModels: PropTypes.arrayOf(PropTypes.string),
    loadingRootModels: PropTypes.bool,
};

PackagesAndModules.defaultProps = {
    onLoad: () => null,
};

export default PackagesAndModules;
